
/**=====================
     74. Job CSS start
==========================**/
.rbt-menu {
  list-style-type: none !important;
}
.select-no-label {
  margin-top: 28px;
}
.filter-cards-view{
  .job-filter{
    &:first-child{
      margin-bottom: 30px;
    }
  }
  .checkbox-animated{
    margin-top: 30px;
    margin-bottom: 30px;
  }
  p{
    color: $light-text;
  }
}
.location-checkbox{
  span{
    color: $light-text;
    padding-left: 33px;
  }
}
.job-accordion{
  .card{
    margin-bottom: 30px;
    .card-header {
      padding: 0.75rem 1.25rem !important;
    }
  }
  .btn-block {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}
.job-search{
  p{
    margin-top: 30px;
  }
  .d-flex{
    .flex-grow-1{
      h6{
        margin-bottom: 3px;
        span:not(.badge){
          font-size: 13px;
          color: $light-text;
          font-weight: normal;
          padding-top: 2px;
          &.badge{
            color: $white;
          }
        }
      }
      p{
        margin-top: 0;
        color: $light-text;
        span:not(.badge){
            margin-left: 10px;
            display: inline-block;
        }
      }
    }
  }
  .job-description{
    margin-top: 30px;
    .react-datepicker-wrapper {
      .react-datepicker__input-container {
        input {
          border: 1px solid $light-semi-gray !important;
          color: $dark-gray !important;
        }
      }
    }
    h6{
      font-weight: 600;
    }
    p{
      margin-top: 0;
      margin-bottom: 6px;
    }
    ul{
      list-style-type: disc;
      list-style-position: inside;
      li{
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
    .theme-form{
      margin-top: 30px;
      margin-bottom: 30px;
      .row{
        div[class^="col-"]{
            + div[class^="col-"]{
            .select2-container{
              margin-top: 14px;
            }
          }
          .job-select2{
            ~ .select2-container{
              margin-top: 0 !important;
            }
          }
          .input-group{
            .datepicker-here{
              font-size: 14px;
              border: 1px solid $light-color;
              padding-top: 20px;
              padding-bottom: 20px;
            }
          }
        }
      }
      input[type='file'] {
        height: calc(2.25rem + 5px);
      }
    }
  }
}
.select2-container {
  .select2-selection--single {
    border-radius: 0.25rem !important;
    border-color: #f6f7fb !important;
    height: 40px !important;
    padding: 5px;
  }
}
.job-pagination{
  margin-bottom: 30px;
  nav{
    ul{
      justify-content: center;
    }
  }
}
@media only screen and (max-width: 991px) {
  .filter-cards-view{
    padding: 20px !important;
    .job-filter{
      &:first-child{
        margin-bottom: 20px;
      }
    }
    .checkbox-animated{
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }
  .job-accordion{
    .animate-chk{
      padding: 20px !important;
    }
  }
  .job-search{
    p {
      margin-top: 20px;
    }
  }
}
@media only screen and (max-width: 575px) {
  .select-no-label {
    margin-top: 0;
  }
  .job-search {
    .d-flex {
      .flex-grow-1 {
        h6 {
          .pull-right {
            .btn {
              margin-top: 4px;
            }
          }
        }
      }
    }
  }
  
  .filter-cards-view{
    padding: 15px !important;
    .job-filter{
      &:first-child{
        margin-bottom: 15px;
      }
    }
    .checkbox-animated{
      margin-top: 15px;
      margin-bottom: 15px;
    }
  }
  .job-accordion{
    .animate-chk{
      padding: 15px !important;
    }
  }
  .job-search{
    p {
      margin-top: 15px;
    }
    .d-flex {
      display: block !important;
      text-align: center;
      .flex-grow-1{
        h6{
          .pull-right{
            float: none;
            margin-top: 3px;
          }
          a{
            display: block;
          }
        }
        p{
          span{
            + span{
              display: block;
            }
          }
        }
      }
      img{
        margin-bottom: 15px;
      }
      .m-r-20{
        margin-right: 0;
      }
    }
    .job-description {
      margin-top: 15px;
      .theme-form {
        margin-top: 15px;
        margin-bottom: 15px;
        .row{
          div[class^="col-"]{
            + div[class^="col-"]{
              .select2-container{
                margin-top: 2px;
              }
            }
          }
        }
      }
    }
  }
  .job-pagination{
    margin-bottom: 15px;
  }
}
@media only screen and (max-width: 360px) {
  .job-search{
    .job-description{
      .btn{
        padding: 6px 12px;
      }
    }
  }
}
/**=====================
     74. Job CSS end
==========================**/
