%chart-1 {
  width: 70px;
  height: 70px;
  margin-left: 15px;
  margin-top: 15px;
  line-height: 70px;
}

%chart-2 {
  width: 35px;
  height: 35px;
  margin-left: 7.5px;
  margin-top: 7.5px;
  line-height: 35px;
}

%chart-3 {
  width: 20px;
  height: 20px;
  margin-left: 5.5px;
  margin-top: 4.5px;
  line-height: 21px;
}

.chart-block {
  canvas {
    width: 100% !important;
  }

  .chart-overflow {
    overflow: hidden;
    text-align: center;
  }
}

.candidcahrt {
  width: 100%;
  height: 470px;
}

.flot-chart-container {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;

  // height: 400px;
  #toggling-series-flot {
    width: 79% !important;
  }
}
