.flot-chart-placeholder {
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 1.2em;
  text-align: center;

  .legend table {
    border-spacing: 5px;
  }

  &.order-graph {
    svg {
      height: 100%;
      width: 100%;
    }
  }
}

.morris-hover {
  position: absolute;
  z-index: 7;

  &.morris-default-style {
    border-radius: 10px;
    padding: 6px;
    color: $dark-gray;
    background: $white;
    border: solid 1px $light-gray;
    font-family: $font-serif;
    font-size: 12px;
    text-align: center;

    .morris-hover-row-label {
      font-weight: bold;
      margin: 0.25em 0;
    }

    .morris-hover-point {
      white-space: nowrap;
      margin: 0.1em 0;
    }
  }
}

.chart-sparkline {
  position: absolute;
  left: 0;
  top: 0;
  visibility: hidden;
  background: rgba(49, 49, 49, 0.7);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
  color: $white;
  font: 10px $font_arial, $font-serif;
  text-align: left;
  white-space: nowrap;
  padding: 5px;
  border: 1px solid rgba(49, 49, 49, 0.75);
  box-sizing: content-box;
  z-index: 10000;
  border-radius: 5px;

  .jqsfield {
    color: $white;
    font: 10px $font_arial, $font-serif;
    text-align: left;
  }
}

.radial-bar {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  background-color: transparent;
  box-sizing: content-box;
  width: 80px;
  height: 80px;
  font-size: 18px;
  font-family: $font-work-sans, $font-serif;
  background-clip: content-box;

  &:after {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    content: attr(data-label);
    background-color: $white;
    z-index: 1;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }

  > img {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    z-index: 3;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }

  &.radial-bar-75 {
    background-image: linear-gradient(360deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }

  &.radial-bar-80 {
    background-image: linear-gradient(378deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }

  &.radial-bar-90 {
    background-image: linear-gradient(414deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }

  &.radial-bar-95 {
    background-image: linear-gradient(432deg, $dark-primary 50%, transparent 50%, transparent), linear-gradient(270deg, $dark-primary 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}

.radial-bar-primary {
  &.radial-bar-75 {
    background-image: linear-gradient(360deg, $primary-color 50%, transparent 50%, transparent), linear-gradient(270deg, $primary-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }

  &.radial-bar-80 {
    background-image: linear-gradient(378deg, $primary-color 50%, transparent 50%, transparent), linear-gradient(270deg, $primary-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }

  &.radial-bar-90 {
    background-image: linear-gradient(414deg, $primary-color 50%, transparent 50%, transparent), linear-gradient(270deg, $primary-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }

  &.radial-bar-95 {
    background-image: linear-gradient(432deg, $primary-color 50%, transparent 50%, transparent), linear-gradient(270deg, $primary-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}

.pieLabel {
  div {
    color: $white !important;

    input[type="checkbox"] {
      margin: 7px;
    }
  }
}

// knob chart css
.knob-chart {
  min-height: 255px;

  .chart-clock-main {
    position: relative;
    width: 200px;
    margin: 45px auto;

    .clock-large {
      position: absolute;
      left: -48px;
      top: -45px;
    }

    .clock-medium {
      position: absolute;
      left: 2px;
      top: 5px;
    }

    .clock-small {
      position: absolute;
      left: 15px;
      top: 55px;
    }
  }
}

// peity chart css
.peity-charts {
  svg {
    width: 100%;
    height: 250px;
  }

  &.peity-chart-primary {
    svg {
      polygon {
        fill: rgba(68, 102, 242, 0.2);
      }

      polyline {
        stroke: $primary-color;
      }
    }
  }
}

p {
  &.data-attributes {
    svg {
      margin-right: 10px;
    }
  }
}

// knob chart
.knob-block {
  position: relative;
  margin-bottom: 0;

  input {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin-left: 0 !important;
    margin-top: -3px !important;
  }

  div {
    canvas {
      position: relative !important;
    }
  }
}

.ct-series-a .ct-point,
.ct-series-a .ct-line,
.ct-series-a .ct-bar,
.ct-series-a .ct-slice-donut {
  stroke: $primary-color;
}

