/**=====================
     01. General CSS Start
==========================**/
//Custom css Start


body {
    background-color: $body-bg-color;
    font-size: $body-font-size;
    overflow-x: hidden;
    font-family: $font-work-sans, $font-serif;
    color: $theme-body-font-color;
}

ul {
    padding-left: $ul-padding-left;
    list-style-type: none;
    margin-bottom: 0;
    padding-right: $ul-padding-right;
}

* a {
    color: $primary-color;

    &.btn-primary,
    &.btn-secondary,
    &.btn-success,
    &.btn-info,
    &.btn-warning,
    &.btn-danger {
        color: $white;
    }
}

*.btn:focus {
    box-shadow: $btn-focus-box-shadow !important;
}

p {
    font-size: $paragraph-font-size;
    line-height: $paragraph-line-height;
    letter-spacing: $paragraph-letter-spacing;
}

code {
    color: $code-tag-color !important;
    background-color: $code-tag-bg-color;
    padding: $code-tag-padding;
    margin: $code-tag-margin;
    border-radius: $code-tag-border-radious;
}

blockquote {
    border-left: $blockquote-border;
    padding: $blockquote-padding;
}

blockquote {
    &.text-center {
        border: none;
        padding: $blockquote-padding;
    }
}

blockquote {
    &.text-end {
        border-left: none;
        border-right: $blockquote-border;
        padding: $blockquote-padding;
    }
}

:focus {
    outline-color: $all-focus-outline-color;
}

.disabled {
    cursor: not-allowed;
    opacity: 0.5;
}

svg {
    vertical-align: baseline;
}

input {
    &:focus {
        outline-color: $transparent-color;
    }
}

:not(pre) {

    >code[class*=language-],
    pre[class*=language-] {
        background: $light-color;
    }
}

.code-box-copy__btn {
    background-color: rgba($primary-color, 0.20);
    border: 1px solid rgba($primary-color, 0.30);
}

//Custom css End

//Typography css start

.typography {

    h1,
    .h1,
    h2,
    .h2,
    h3,
    .h3,
    h4,
    .h4,
    h5,
    .h5,
    h6,
    .h6 {
        margin: 20px 0;
        margin-top: 0;

        &:last-child {
            margin-bottom: 0;
        }
    }

    small {
        padding-left: $small-tag-padding;
        color: $small-tag-color;
    }
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    line-height: 1.2;
}

.h1 {
    font-size: 2.5rem;
}

.h2 {
    font-size: 2rem;
}

.h3 {
    font-size: 1.75rem;
}

.h4 {
    font-size: 1.5rem;
}

.h5 {
    font-size: 1.25rem;
}

.h6 {
    font-size: 1rem;
}

pre {
    background-color: $pre-tag-bg-color;
    padding: $pre-tag-padding;
}

a {
    &:hover {
        text-decoration: none;
        color: $primary-color;
    }
}

//Typography css end

.owl-theme {
    .owl-dots {
        .owl-dot {
            span {
                background: lighten($primary-color, 20%);
            }

            &.active,
            &:hover {
                span {
                    background: $primary-color;
                }
            }
        }
    }

    .owl-nav {
        &.disabled {
            &+.owl-dots {
                margin-bottom: -10px;
                margin-top: 25px;
            }
        }
    }
}

a {
    text-decoration: none;
}

#external-events {
    width: 20%;
}

.cke_bottom {
    display: none !important;
}

// sweetalert
.lead {
    margin-bottom: 5px;
    font-size: 16px;
}

.sweet-alert {
    .btn-link {
        color: $primary-color;
    }
}

.editor-toolbar {
    opacity: .4;

    &:hover {
        opacity: .8;
    }
}

.click2edit {
    &~.note-editor {
        &.note-frame {
            margin-bottom: 30px;
        }
    }
}

.swal2-styled.swal2-confirm {
    background-color: $primary-color !important;

    &:focus {
        box-shadow: 0 0 0 3px rgb($primary-color, 0.5) !important;
    }
}

.note-editor {
    &.note-frame {
        border-color: $light-color;
    }
}

.note-editor {
    &.note-frame {
        border-color: $light-color;
    }
}

// modal page
.modal-dialog {
    .modal-content {
        .modal-body {
            p {
                a {
                    margin-right: 0;
                }
            }
        }
    }
}

// index page notify
.alert-theme {
    button {
        top: 33px !important;
        right: 30px !important;
        color: $primary-color;
    }

    span {
        +span {
            +span {
                border: 1px solid $primary-color;
                background-color: $white;
                border-radius: 4px;
            }
        }
    }

    i {
        background-color: $primary-color;
        color: $white;
        padding: 20px;
        margin-right: 20px !important;
    }
}

.alert-copy {
    border: 1px solid $primary-color;
    line-height: 1;
    display: inline-block;
    width: 300px;
    color: $white;
    background-color: $primary-color;
    box-shadow: 3px 3px 5px 0 #9b9b9b;

    .close {
        box-shadow: none;
        color: $white;
        line-height: 1.4;
        font-weight: 100;
    }
}

