.ace-editor {
    height: 400px;
    width: 100%;
}

.editor-toolbar {
    width: 100% !important;
}

.CodeMirror {
    top: 0 !important;
}

// vertical menu
.pixelstrap {
    ul {
        a {
            color: $theme-body-sub-title-color;

            &:hover,
            &:focus,
            &:active {
                color: $primary-color;
            }
        }
    }
}

// contact
.contact-profile-form {
    padding-bottom: 30px;
}

.contact-search {
    .form-group {
        width: 100%;

        .form-control {
            height: 35px;
        }

        &:before {
            left: 67px !important;
            top: 5px !important;
        }

        &:after {
            left: 37px !important;
            top: 8px !important;
        }
    }
}

.contact-filter {
    display: flex;
    align-items: center;

    .input-range,
    h5 {
        margin-top: -15px;
    }

    h5 {
        width: 224px;
    }
}

.contact-table {
    table {

        th,
        td {
            border-top: none;
            padding: 30px;
            vertical-align: middle;
        }

        tr {
            td {
                border-bottom: 1px solid $light-semi-gray;

                h6 {
                    margin-bottom: 0;
                }

                .btn-primary {
                    color: $white;

                    &:hover {
                        color: $white;
                    }
                }
            }

            &:last-child {
                td {
                    border-bottom: none;
                }
            }
        }
    }
}

.change-img-button {
    margin: 0 auto;
}

.contact-profile {
    position: relative;

    .icon-wrapper {
        position: absolute;
        top: 73%;
        left: 53%;
        padding: 10px;
        background-color: #fff;
        border-radius: 25px;
        cursor: pointer;
    }
}

.upload {
    position: absolute;
    width: 36px;
    left: 0;
    right: 0;
    opacity: 0;
}

.search-not-found {
    align-items: center;
    justify-content: center;
    height: 631px;
    display: flex;

    .second-search {
        width: 200px;
    }
}


/**=====================
      01. General CSS Ends
==========================**/