/**=====================
   55. theme layout CSS Start
==========================**/
//Sidebar color scss
$color1: #405365;
$color2: #1c1d1e;
$color3: #0c0c3c;
$color4: #0747a6;
$color5: #4d3096;
@each $bg-name, $bg-color in (color1, $color1), (color2, $color2), (color3, $color3), (color4, $color4),
	(color5, $color5), (light, $light-color), (dark, #2f3c4e)
{
	.bg-#{$bg-name} {
		background-color: $bg-color !important ;
	}
}
$i: 1;
@while $i<=6 {
	.bg-pattern#{$i} {
		background-image: url(../../images/sidebar-pattern/#{$i}.png) !important;
		background-color: #2f3c4e;
	}
	.bg-img#{$i} {
		background-image: url(../../images/sidebar-image/#{$i}.jpg) !important;
	}
	$i: $i + 1;
}
.page-wrapper {
	.page-body-wrapper {
		.page-sidebar {
			@each $sidebar-name, $sidebar-color in (color1, $color1), (color2, $color2), (color3, $color3),
				(color4, $color4), (color5, $color5), (light, $light-color), (dark, #2f3c4e)
			{
				&.#{$sidebar-name}-sidebar {
					background-color: $sidebar-color;
					.sidebar-menu {
						> li {
							> a {
								&:hover {
									color: $white;
								}
							}
						}
						.sidebar-submenu {
							> li {
								> a {
									&:hover,
									&.active {
										color: $white;
									}
								}
								&.active {
									> a {
										&.active {
											color: $white;
										}
									}
								}
							}
						}
					}
					@if ($sidebar-name == light) {
						.sidebar-user {
							h6 {
								color: $primary-color;
							}
							p {
								color: $theme-body-font-color;
							}
						}
						.sidebar-menu {
							> li {
								> a {
									color: $theme-body-font-color;
									&:hover {
										color: $primary-color;
									}
								}
							}
							.sidebar-submenu {
								> li {
									> a {
										color: $theme-body-font-color;
										&:hover,
										&.active {
											color: $primary-color;
										}
									}
									&.active {
										> a {
											&.active {
												color: $primary-color;
											}
										}
									}
								}
							}
						}
					}
				}
			}
			$i: 1;
			@while $i<=6 {
				&.sidebar-pattern#{$i} {
					background-image: url(../../images/sidebar-pattern/#{$i}.png);
					background-color: #2f3c4e !important;
				}
				&.sidebar-img#{$i} {
					background-image: url(../../images/sidebar-image/#{$i}.jpg);
				}
				$i: $i + 1;
			}
		}
	}
}
//Sidebar settings
.page-wrapper {
	.page-body-wrapper {
		.page-sidebar {
			&[sidebar-layout="border-sidebar"] {
				.sidebar-header {
					border-bottom: 1px solid rgba(124, 124, 124, 0.15);
					transition: all 0.3s ease;
				}
			}
			&[sidebar-layout="iconcolor-sidebar"] {
				.sidebar-menu {
					li {
						$colors: $success-color, $secondary-color, $primary-color, $danger-color, $info-color,
							$warning-color, $light-color;
						$repeat: 100;
						@for $i from 1 through $repeat {
							&:nth-child(#{length($colors)}n + #{$i}) {
								svg {
									color: lighten(nth($colors, random(length($colors))), 15%);
								}
								a {
									&:hover {
										> span {
											color: lighten(nth($colors, random(length($colors))), 15%);
										}
									}
								}
							}
						}
					}
				}
				&.light-sidebar {
					.sidebar-menu {
						li {
							$colors: $success-color, $secondary-color, $primary-color, $danger-color, $info-color,
								$warning-color, $dark-color;
