//Dark layout
body {
	&.light-only {
		.page-wrapper {
			.page-body-wrapper {
				.page-sidebar {
					background-color: $white;
					.sidebar-user {
						h6 {
							color: $primary-color;
						}
						p {
							color: $theme-body-font-color;
						}
					}
					.sidebar-menu {
						> li {
							> a {
								color: $theme-body-font-color;
								&:hover {
									color: $primary-color;
								}
							}
						}
						.sidebar-submenu {
							> li {
								> a {
									color: $theme-body-font-color;
									&:hover,
									&.active {
										color: $primary-color;
									}
								}
								&.active {
									> a {
										&.active {
											color: $primary-color;
										}
									}
								}
							}
						}
					}
					.sidebar {
						&:hover {
							&.custom-scrollbar {
								&::-webkit-scrollbar-thumb {
									background-color: rgba(0, 0, 0, 0.15);
								}
							}
						}
					}
				}
				.page-body {
					background-color: $light-color;
				}
			}
		}
	}
	&.dark-only {
		color: $dark-all-font-color;
		background-color: $dark-body-background;
		.offcanvas-bookmark {
			&.page-wrapper {
				.page-body-wrapper {
					.page-body {
						.bookmark {
							ul {
								li {
									.Typeahead-menu {
										a {
											color: $dark-small-font-color;
										}
									}
								}
							}
						}
					}
				}
			}
		}
		.ProfileCard-realName {
			a,
			span {
				color: $dark-small-font-color;
			}
		}
		.ProfileCard {
			&:hover {
				background: $dark-border-color;
			}
		}
		.kUKEuI {
			background-color: $dark-body-background;
		}
		.bqNMrp {
			border-bottom: 1px solid $dark-border-color;
		}
		.vertical-timeline-element-content {
			background-color: $dark-border-color !important;
			.vertical-timeline-element-content-arrow {
				border-right: 7px solid $dark-border-color !important;
			}
		}
		.vertical-timeline-element-icon {
			box-shadow: 0 0 0 4px $dark-border-color, inset 0 2px 0 rgba($black, 0.08), 0 3px 0 4px rgba($black, 0.05);
		}
		.vertical-timeline {
			&::before {
				background: $dark-border-color !important;
			}
		}
		.sweet-alert,
		.reactour__helper,
		.fileContainer,
		.ceCBVJ {
			background-color: $dark-card-background !important;
		}
		.rbc-month-view,
		.rbc-time-view {
			border: 1px solid $dark-border-color;
		}
		.react-datepicker{
			background-color: $dark-body-background;
			.react-datepicker__day-name, .react-datepicker__day{
				color: $dark-all-font-color;
			}
			.react-datepicker__header{
				border-bottom: 1px solid $dark-border-color;
			}
		}
		.crm-activity{
			ul.dates{
				li{
					+ li{
						border: 1px solid $dark-border-color;
